<template>
    <div class="teams-noteam-menu">
		<div class="teams-noteam-nav">
			<button @click="SelectNoTeamPage('createTeam')">Opret Team</button>
			<button @click="SelectNoTeamPage('TeamInvites')">Invites</button>
			<button @click="SelectNoTeamPage('TeamHub')">Team Hub</button>
		</div>
		<div class="teams-noteam-content" v-if="NoTeamPage === 'createTeam'">
			<img src="img/logo.png" alt="logo">
			<h1>Du er ikke en del af et team</h1>
			<button @click="CreateTeam()">opret Team</button>
		</div>
		<div class="teams-invites" v-if="NoTeamPage === 'TeamInvites'">
            <div class="invites-noteam" v-if="IsObjectEmpty(TeamInvites)">
				<img src="img/logo.png" alt="logo">
				<h1>Du har ingen invites</h1>
			</div>
			<div class="invites-main" v-if="!IsObjectEmpty(TeamInvites)">
				<div class="invites-header">
					<h1>Invitationer</h1>
				</div>
				<div class="invites-content">
					<div class="invites">
                        <div class="invite" v-for="(invite) in TeamInvites" :key="invite.uuid">
                            <p>{{invite.name}}</p>
                            <button @click="AccecptInvite(invite)" class="green">Acceptere Invite</button>
                            <button @click="DeclineInvite(invite)">Afvis Invite</button>
                        </div>
                    </div>
				</div>
			</div>
		</div>
		<div class="teams-hub" v-if="NoTeamPage === 'TeamHub'">
            <div class="hub-noteam" v-if="IsObjectEmpty(OpenTeams)">
				<img src="img/logo.png" alt="logo">
				<h1>Der er pt. ingen åbne Teams</h1>
			</div>
			<div class="hub-main" v-if="!IsObjectEmpty(OpenTeams)">
                <div class="hub-subject" v-for="(team) in OpenTeams" :key="team.uuid">
                    <button :class="team.isOpen ? 'collapsible active' : 'collapsible'" @click="toggleTeamDiv(team)">
                        <p class="text">{{team.name}}</p>
                        <p class="text members_count">Medlemmer: {{team.members.length}}/{{MaxMembers}}</p>
                    </button>
                    <div :class="team.isOpen ? 'hub-content active' : 'hub-content'">
                        <ol>
                            <li v-for="(member, index) in team.members" :key="member">
                                <p>- {{member.name}} ({{index + 1}})</p>
                                </li>
                            </ol>
                        <div class="hub-content-footer">
                            <button @click="JoinTeam(team)" class="blue">Join Team</button>
                        </div>
                    </div>
                </div>
            </div>
		</div>
	</div>
</template>

<script>

import axios from 'axios';
import Swal from 'sweetalert2'

export default {
    props: {
        StartLoading: {},
        OpenTeams: {},
        TeamInvites: {},
        MaxMembers: {},
        NameResource: {},
        close: {},
    },

    data: function(){
        return({
            NoTeamPage: 'createTeam',
        })
    },

    methods: {
        receiveLua: function(event){
            if(!event || !event.data) return;
            //const lua = event.data;
        },

        SelectNoTeamPage: function(page) {
            this.NoTeamPage = page;
        },

        IsObjectEmpty: function(obj) {
            return Object.keys(obj).length <= 0;
        },

        toggleTeamDiv: function(team) {
            team.isOpen = !team.isOpen;
        },

        CreateTeam: function() {
            (async () => {
                const { 
                    value: text 
                } = await Swal.fire({
                    title: 'Navn på dit Team',
                    input: 'text',
                    inputPlaceholder: 'Skriv navnet på dit Team her...',
                    showCancelButton: true
                })
                
                if (text) {
                    const NameResource = this.NameResource[0];
                    let name = text;

                    this.StartLoading("opretter team", 5000);
                    
                    axios.post(`https://${NameResource}/CreateTeam`, { 
                        team_name: name 
                    }).catch(function (error) {
                        console.log(error + ' : Error at CreateTean');
                    });
                }
            })()
        },

        JoinTeam: function(team) {
            const NameResource = this.NameResource[0];
            axios.post(`https://${NameResource}/jointeamHub`, { 
                uuid: team.uuid 
            }).catch(function (error) {
                console.log(error + ' : Error at jointeamHub');
            });
        },

        AccecptInvite: function(invite) {
            const NameResource = this.NameResource[0];
            axios.post(`https://${NameResource}/acceptInvite`, { 
                uuid: invite.uuid 
            }).catch(function (error) {
                console.log(error + ' : Error at newTimeLap');
            });
        },

        DeclineInvite: function(invite) {
            const NameResource = this.NameResource[0];
            axios.post(`https://${NameResource}/declineInvite`, { 
                uuid: invite.uuid 
            }).catch(function (error) {
                console.log(error + ' : Error at newTimeLap');
            });
        },

        keyPress: function(event) {
            const key = event.key;
            
            if(key == "Escape"){
                this.close();
            }
        },
    },

    created: function(){
        window.addEventListener('message', this.receiveLua);  
        window.addEventListener('keydown', this.keyPress);
    },
    
    destroyed: function(){
        window.removeEventListener('message', this.receiveLua);
        window.removeEventListener('keydown', this.keyPress);
    },
}

</script>

<style>
.teams-noteam-menu {
    display: flex;
    flex-flow: column;
    width: 100%;
}

.teams-noteam-nav {
    display: flex;
    padding: 10px 0;
    border-bottom: 2px solid #575757;
}

.teams-noteam-nav button {
    flex: 1;
    padding: 5px;
    outline: none;
    border: none;
    text-transform: uppercase;
    font-weight: bold;
    font-size: 14px;
    background-color: #c32222;
    color: #e7e7e7;
    height: 30px;
    border-right: 1px solid #A62222;
}

.teams-noteam-nav button:first-child {
    border-top-left-radius: 3px;
    border-bottom-left-radius: 3px;
}

.teams-noteam-nav button:last-child {
    border-top-right-radius: 3px;
    border-bottom-right-radius: 3px;
    border: none;
}

.teams-noteam-content {
    display: flex;
    flex-flow: column;
    margin: auto;
    align-items: center;
    justify-content: center;
    height: calc(100vh - 100px);
}

.teams-noteam-content img {
    width: 200px;
    margin-bottom: 10px;
}

.teams-noteam-content button {
    margin-top: 15px;
    background-color: #c32222;
    border-radius: 3px;
    color: #e7e7e7;
    padding: 10px 40px;
    outline: none;
    border: none;
    text-transform: uppercase;
    font-weight: bold;
    font-size: 18px;
}

.teams-noteam-content h1 {
    font-size: 1.5em;
    text-transform: uppercase;
}

 .invites-main {
    display: flex;
    flex-flow: column;
    background: #575757;
    width: 100%;
    border-radius: 3px;
    margin: 10px 0;
}

.invites-main .invites-header {
    padding: 8px;
    text-align: start;
    background: #424242;
}

.invites-main .invites-header h1 {
    font-size: 20px;
    text-transform: uppercase;
}

.invites-main .invites-content {
    padding: 4px;
    display: flex;
    justify-content: space-around;
}

.invites-main .invites {
    width: 100%;
    border-top: 1px solid #2e2e2e;
}

.invites-main .invites .invite {
    display: flex;
    align-items: center;
    border-bottom: 1px solid #2e2e2e;
}

.invites-main .invites .invite p {
    flex: 1;
    font-size: 18px;
    word-wrap: break-word;
    text-align: start;
    padding-left: 5px;
}

.invites-main .invites .invite button {
    flex: 1;
    padding: 5px;
    outline: none;
    border: none;
    text-transform: uppercase;
    font-weight: bold;
    font-size: 14px;
    background-color: #c32222;
    border-radius: 3px;
    color: #e7e7e7;
    margin: 5px;
}

.invites-main .invites .invite button.green {
    background-color: #4CAF50;
}

.invites-noteam {
    display: flex;
    flex-flow: column;
    margin: auto;
    align-items: center;
    justify-content: center;
    height: calc(100vh - 100px);
}

.invites-noteam img {
    width: 200px;
    margin-bottom: 10px;
}

.invites-noteam h1 {
    font-size: 1.5em;
    text-transform: uppercase;
}

.hub-main {
    width: 100%;
}

.hub-subject {
    display: flex;
    flex-flow: column;
    background: #575757;
    width: 100%;
    border-radius: 3px;
    margin: 10px 0;
}

.hub-subject button.collapsible {
    display: flex;
    background-color: #424242;
    color: white;
    padding: 8px;
    width: 100%;
    border: none;
    text-align: left;
    outline: none;
    font-weight: bold;
    font-size: 20px;
    text-transform: uppercase;
}

.hub-subject button.collapsible p.text {
    flex: 3;
}

.hub-subject button.collapsible p.members_count {
    margin-right: 10px;
}

.hub-subject button.collapsible:after {
    content: '\002B';
    color: white;
    font-weight: bold;
    float: right;
    margin-left: 5px;
}

.hub-subject button.collapsible.active:after {
    content: "\2212";
}

.hub-subject .hub-content {
    max-height: 0;
    overflow: hidden;
    transition:max-height 0.3s ease-out;
}

.hub-subject .hub-content.active {
    max-height: 1000px;
    transition: max-height 0.3s ease-in;
}

.hub-subject .hub-content ol {
    padding: 5px 0;
    margin: 0 5px;
    border-bottom: 1px solid #424242;
}

.hub-subject .hub-content li {
    display: flex;
    justify-content: space-evenly;
    padding: 5px 0;
    margin: 0 5px;
    text-align: center;
}

.hub-subject .hub-content li p {
    flex: 2;
}

.hub-content-footer {
    display: flex;
    justify-content: space-evenly;
    padding: 5px;
}

.hub-content-footer button {
    flex: 1;
    padding: 5px;
    outline: none;
    border: none;
    text-transform: uppercase;
    font-weight: bold;
    font-size: 14px;
    background-color: #c32222;
    border-radius: 3px;
    color: #e7e7e7;
    margin: 5px;
    display: flex;
    justify-content: center;
}

.hub-content-footer button.green {
    background-color: #4CAF50;
}

.hub-content-footer button.blue {
    background-color: #2196F3;
}

.hub-noteam {
    display: flex;
    flex-flow: column;
    margin: auto;
    align-items: center;
    justify-content: center;
    height: calc(100vh - 100px);
}

.hub-noteam img {
    width: 200px;
    margin-bottom: 10px;
}

.hub-noteam h1 {
    font-size: 1.5em;
    text-transform: uppercase;
}
</style>