<template>
   <div class="teams-team-menu">
        <div class="teams-team-nav">
            <button @click="SelectTeamPage('Team')">Team</button>
            <button @click="SelectTeamPage('Racing')">Racing</button>
        </div>
        <div class="teams-team-header">
            <h1>{{Team.name}} ({{GetTeamSize()}})</h1>
        </div>
        <div class="teams-team-main" v-if="TeamPage === 'Team'">
            <div class="team-subject">
                <div class="team-header">
                    <h1>Handlinger</h1>
                </div>
                <div class="team-content">
                    <button @click="DeleteTeam()" v-if="isTeamOwner()">Slet Team</button>
                    <button @click="LeaveTeam()">Forlad Team</button>
                    <button @click="ChangeName()" v-if="isTeamOwner()">Skift Navn</button>
                    <button :class="!Team.isPublic ? 'green' : ''" @click="TogglePublic()">{{Team.isPublic ? 'Luk Team' : 'Åben Team'}}</button>
                </div>
            </div>
            <div class="team-subject" v-if="isTeamOwner()">
                <div class="team-header">
                    <h1>Tilføj Spiller</h1>
                </div>
                <div class="team-content">
                    <div class="team-invite">
                        <input type="number" class="Input-text" min="1" v-model="inviteSource">
                        <button @click="InvitePlayer()">Inviter</button>
                    </div>
                </div>
            </div>
            <div class="team-subject">
                <div class="team-header">
                    <h1>Medlemmer</h1>
                </div>
                <div class="team-content">
                    <div class="players" v-if="!IsObjectEmpty(Team.members)">
                        <div class="player" v-for="(member) in Team.members" :key="member.source">
                            <p>{{member.name}}</p>
                            <button @click="KickPlayer(member)" v-if="isTeamOwner()">Kick</button>
                            <button @click="MakeLeader(member)" v-if="isTeamOwner()">Gør Leder</button>
                        </div>
                    </div>
                    <div class="no-players" v-if="IsObjectEmpty(Team.members)">
                        <h1>Der er pt. ingen medlemmer</h1>
                    </div>
                </div>
            </div>
            <div class="team-subject">
                <div class="team-header">
                    <h1>Invites</h1>
                </div>
                <div class="team-content">
                    <div class="team-invites" v-if="!IsObjectEmpty(Team.invites)">
                        <div class="invite" v-for="(invite) in Team.invites" :key="invite.source">
                            <p>{{invite.name}}</p>
                            <button @click="RemoveInvite(invite)" v-if="isTeamOwner()">Fjern Invite</button>
                        </div>
                    </div>
                    <div class="team-no-invites" v-if="IsObjectEmpty(Team.invites)">
                        <h1>Der er pt. ingen invites</h1>
                    </div>
                </div>
            </div>
        </div>
        <div class="teams-team-main" v-if="TeamPage === 'Racing'">
            <div class="racing-no-races" v-if="IsObjectEmpty(GetRaces())">
                <img src="img/logo.png" alt="logo">
                <h1>Der er pt. ingen Race</h1>
            </div>
            <div class="racing-ongoing" v-if="!IsObjectEmpty(GetRaces()) && isRacing">
				<img src="img/logo.png" alt="logo">
				<h1>Du er igang med et race</h1>
				<button @click="ExitRace()">forlad race</button>
			</div>
            <div class="racing-main" v-if="!IsObjectEmpty(GetRaces()) && !isRacing">
                <div class="racing-subject" v-for="(race) in GetRaces()" :key="race.id">
                    <button :class="race.isOpen ? 'collapsible active' : 'collapsible'" @click="toggleRaceDiv(race)">
                        <p class="text">{{race.name}}</p>
                        <p :class="race.isActive ? 'pulse red' : 'pulse green'">{{race.isActive ? '● OFFLINE' : '● ONLINE'}}</p>
                    </button>
                    <div :class="race.isOpen ? 'racing-content active' : 'racing-content'">
                        <ol>
                            <li v-for="(time, index) in race.bestTimes" :key="index">
                                <p class="list-number">{{index + 1}}</p>
                                <p class="divider">-</p>
                                <p>{{time.driver}} ({{time.vehicle}})</p>
                                <p class="divider">-</p>
                                <p class="time">{{TransformTime("minutes", time.lapTime)}}:{{TransformTime("seconds", time.lapTime)}}:{{TransformTime("milliseconds", time.lapTime)}}</p>
                            </li>
                        </ol>
                        <div class="racing-content-footer">
                            <button @click="SetRaceGPS(race)" class="blue">Sæt GPS</button>
                            <button @click="PreviewRace(race)" class="blue">Race Preview</button>
                            <button @click="ToggleReady(race)" :class="hasReadyUp(race) ? 'green' : ''">{{hasReadyUp(race) ? 'Afmeld' : 'Klar'}} ({{race.membersReady.length}}/{{GetTeamSize()}})</button>
                            <button @click="StartRace(race)" class="green" v-if="isTeamOwner()">Start Race</button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
   </div>
</template>

<script>
import axios from 'axios';
import Swal from 'sweetalert2'

export default {
    props: {
        StartLoading: {},
        Team: {},
        Races: {},
        source: {},
        playerJob: {},
        isRacing: {},
        prependZero: {},
        MaxMembers: {},
        NameResource: {},
        close: {},
    },

    data: function(){
        return({
            TeamPage: 'Team',
            inviteSource: 1,
            processing: false,
            timeoutTime: 100,
        })
    },

    methods: {
        SelectTeamPage: function(page) {
            this.TeamPage = page;
        },

        isTeamOwner: function() {
            return this.Team.owner.source == this.source;
        },

        IsArrayEmpty: function(array) {
            return array.length <= 0;
        },

        IsObjectEmpty: function(obj) {
            return Object.keys(obj).length <= 0;
        },

        hasReadyUp: function(race) {
            return race.membersReady.includes(this.source);
        },

        GetTeamSize: function() {
            return this.Team.members.length + 1;
        },

        hasWhitelistedJob: function(race) {
            let hasWhitelistJob = false;
            
            if(!this.IsArrayEmpty(race.whitelistedJobs)) {
                race.whitelistedJobs.map((job) => {
                    if(job == this.playerJob) hasWhitelistJob = true;
                });
            }

            if(this.IsArrayEmpty(race.whitelistedJobs)) hasWhitelistJob = true;

            return hasWhitelistJob;
        },

        GetRaces: function() {
            let filteredRaces = [];

            this.Races.map((race) => {
                if(this.hasWhitelistedJob(race)) {
                    filteredRaces.push(race);
                }
            });

            return filteredRaces;
        },

        toggleRaceDiv: function(race) {
            let variable = this;
            if(variable.processing === true) return;
            variable.processing = true;

            race.isOpen = !race.isOpen;
            const NameResource = variable.NameResource[0];
            axios.post(`https://${NameResource}/toggleRaceOpen`, {
                raceID: race.id
            }).catch(function (error) {
                console.log(error + ' : Error at toggleRaceOpen');
            });
            

            setTimeout(() => {
                variable.processing = false;
            }, variable.timeoutTime);
        },

        ToggleReady: function(race) {
            let variable = this;
            if(variable.processing === true) return;
            variable.processing = true;

            let isReady = variable.hasReadyUp(race);
            if(isReady == false) {
                race.membersReady.push(this.source);
            }
            
            if(isReady == true) {
                const index = race.membersReady.indexOf(this.source);
                if (index > -1) {
                    race.membersReady.splice(index, 1);
                }
            }

            const NameResource = variable.NameResource[0];
            axios.post(`https://${NameResource}/toggleReady`, {
                raceID: race.id, 
                isReady: isReady
            }).catch(function (error) {
                console.log(error + ' : Error at toggleReady');
            });

            setTimeout(() => {
                variable.processing = false;
            }, variable.timeoutTime);
        },

        DeleteTeam: function() {
            let variable = this;
            if(variable.processing === true) return;
            variable.processing = true;

            variable.StartLoading("Sletter Teamet", 5000);

            const NameResource = variable.NameResource[0];
            axios.post(`https://${NameResource}/deleteTeam`).catch(function (error) {
                console.log(error + ' : Error at deleteTeam');
            });

            setTimeout(() => {
                variable.processing = false;
            }, variable.timeoutTime);
        },

        LeaveTeam: function() {
            let variable = this;
            if(variable.processing === true) return;
            variable.processing = true;

            variable.StartLoading("Forlader Teamet", 5000);

            const NameResource = variable.NameResource[0];
            axios.post(`https://${NameResource}/leaveTeam`).catch(function (error) {
                console.log(error + ' : Error at leaveTeam');
            });

            setTimeout(() => {
                variable.processing = false;
            }, variable.timeoutTime);
        },

        ChangeName: function() {
            (async () => {
                const { 
                    value: text 
                } = await Swal.fire({
                    title: 'Nyt navn på dit Team',
                    input: 'text',
                    inputPlaceholder: 'Skriv navnet på dit Team her...',
                    showCancelButton: true
                })
                
                if (text) {
                    const NameResource = this.NameResource[0];
                    let name = text;

                    this.Team.name = name;
                    this.StartLoading("Ændre navn på teamet", 5000);
                    
                    axios.post(`https://${NameResource}/changeName`, { 
                        team_name: name 
                    }).catch(function (error) {
                        console.log(error + ' : Error at changeName');
                    });
                }
            })()
        },

        TogglePublic: function() {
            let variable = this;
            if(variable.processing === true) return;
            variable.processing = true;

            variable.Team.isPublic = !variable.Team.isPublic

            const NameResource = variable.NameResource[0];
            axios.post(`https://${NameResource}/publicTeam`).catch(function (error) {
                console.log(error + ' : Error at publicTeam');
            });

            setTimeout(() => {
                variable.processing = false;
            }, variable.timeoutTime);
        },

        KickPlayer: function(member) {
            let variable = this;
            if(variable.processing === true) return;
            variable.processing = true;

            let player_id = member.source;
            const NameResource = variable.NameResource[0];
            axios.post(`https://${NameResource}/kickPlayer`, { 
                player_id: player_id 
            }).catch(function (error) {
                console.log(error + ' : Error at kickPlayer');
            });

            setTimeout(() => {
                variable.processing = false;
            }, variable.timeoutTime);
        },

        MakeLeader: function(member) {
            let variable = this;
            if(variable.processing === true) return;
            variable.processing = true;

            let player_id = member.source;
            variable.StartLoading("Gør " + member.name + " til leder af " + variable.Team.name, 5000);

            const NameResource = variable.NameResource[0];
            axios.post(`https://${NameResource}/transferOwnership`, { 
                player_id: player_id 
            }).catch(function (error) {
                console.log(error + ' : Error at transferOwnership');
            });

            setTimeout(() => {
                variable.processing = false;
            }, variable.timeoutTime);
        },

        InvitePlayer: function() {
            let variable = this;
            if(variable.processing === true) return;
            variable.processing = true;

            let source = variable.inviteSource;
            let teamSize = variable.GetTeamSize();

            if(variable.MaxMembers <= teamSize) {
                variable.$notify({
                    group: 'alert',
                    type: 'error',
                    title: 'FEJL',
                    text: 'Teamet er for stort. Maks: ' + variable.MaxMembers
                });
                return
            }

            const NameResource = variable.NameResource[0];
            axios.post(`https://${NameResource}/invitePlayer`, { 
                player_id: source 
            }).catch(function (error) {
                console.log(error + ' : Error at invitePlayer');
            });

            setTimeout(() => {
                variable.processing = false;
            }, variable.timeoutTime);
        },

        RemoveInvite: function(invite) {
            let variable = this;
            if(variable.processing === true) return;
            variable.processing = true;

            const NameResource = variable.NameResource[0];
            axios.post(`https://${NameResource}/removeInvite`, { 
                player_id: invite.source, 
                uuid: this.Team.uuid, 
            }).catch(function (error) {
                console.log(error + ' : Error at removeInvite');
            });

            setTimeout(() => {
                variable.processing = false;
            }, variable.timeoutTime);
        },

        SetRaceGPS: function(race) {
            let variable = this;
            if(variable.processing === true) return;
            variable.processing = true;

            const NameResource = variable.NameResource[0];
            axios.post(`https://${NameResource}/setGPS`, { 
                raceID: race.id 
            }).catch(function (error) {
                console.log(error + ' : Error at setGPS');
            });

            this.close();

            setTimeout(() => {
                variable.processing = false;
            }, variable.timeoutTime);
        },

        PreviewRace: function(race) {
            let variable = this;
            if(variable.processing === true) return;
            variable.processing = true;

            const NameResource = variable.NameResource[0];
            axios.post(`https://${NameResource}/previewRace`, { 
                raceID: race.id 
            }).catch(function (error) {
                console.log(error + ' : Error at previewRace');
            });

            this.close();

            setTimeout(() => {
                variable.processing = false;
            }, variable.timeoutTime);
        },

        StartRace: function(race) {
            let variable = this;
            if(variable.processing === true) return;
            variable.processing = true;

            let ready = race.membersReady.length;
            let teamSize = variable.GetTeamSize();

            if(ready < teamSize) {
                variable.$notify({
                    group: 'alert',
                    type: 'error',
                    title: 'FEJL',
                    text: 'Der er ikke nok klar til at starte racet.'
                });
                return
            }

            setTimeout(() => {
                variable.processing = false;
            }, variable.timeoutTime);

            (async () => {
                const { 
                    value: text 
                } = await Swal.fire({
                    title: 'Antal omgange',
                    input: 'text',
                    inputPlaceholder: 'Skriv antal omgange i løbet...',
                    showCancelButton: true
                })
                
                if (text) {
                    let laps = text;
                    const NameResource = variable.NameResource[0];
                    axios.post(`https://${NameResource}/startRace`, { 
                        raceID: race.id, 
                        laps: laps 
                    }).catch(function (error) {
                        console.log(error + ' : Error at startRace');
                    });
                }
            })()
        },

        ExitRace: function() {
            Swal.fire({
                title: 'Vil du forlade racet?',
                showDenyButton: false,
                showCancelButton: true,
                confirmButtonText: `Ja`,
                cancelButtonText: `Nej`,
            }).then((result) => {
                if (result.isConfirmed) {
                    const NameResource = this.NameResource[0];
                    axios.post(`https://${NameResource}/exitRace`).catch(function (error) {
                        console.log(error + ' : Error at exitRace');
                    });
                }
            })
        },

        TransformTime: function(type, lapTime) {
            let variable = this;
            let timeElapsed = lapTime;

            let minutes = parseInt(timeElapsed / 1000 / 60);
            if (minutes >= 60) minutes %= 60;
                                    
            let seconds = parseInt(timeElapsed / 1000);
            if (seconds >= 60) seconds %= 60;
                                    
            let milliseconds = timeElapsed;
            if (milliseconds >= 1000) milliseconds %= 1000;

            let returnValue = undefined;
            if(type == "minutes") {
                returnValue = variable.prependZero(minutes, 2);
            }

            if(type == "seconds") {
                returnValue = variable.prependZero(seconds, 2);
            }

            if(type == "milliseconds") {
                returnValue = variable.prependZero(milliseconds, 2);
            }
            
            return returnValue;
        },

        keyPress: function(event) {
            const key = event.key;
            
            if(key == "Escape"){
                this.close();
            }
        },
    },

    created: function(){
        window.addEventListener('keydown', this.keyPress);
    },
    
    destroyed: function(){
        window.removeEventListener('keydown', this.keyPress);
    },
}
</script>

<style>
.teams {
    transition: 0.6s ease-in;
}

.teams-team-menu {
    display: flex;
    flex-flow: column;
    width: 100%;
}

.teams-team-nav {
    display: flex;
    padding: 10px 0;
    border-bottom: 2px solid #575757;
}

.teams-team-nav button {
    flex: 1;
    padding: 5px;
    outline: none;
    border: none;
    text-transform: uppercase;
    font-weight: bold;
    font-size: 14px;
    background-color: #c32222;
    color: #e7e7e7;
    height: 30px;
    border-right: 1px solid #A62222;
}

.teams-team-nav button:first-child {
    border-top-left-radius: 3px;
    border-bottom-left-radius: 3px;
}

.teams-team-nav button:last-child {
    border-top-right-radius: 3px;
    border-bottom-right-radius: 3px;
    border: none;
}

.teams-team-header {
    padding: 10px 0;
    border-bottom: 2px solid #575757;
}

.teams-team-header h1 {
    text-transform: uppercase;
    font-size: 22px;
}

.teams-team-menu .teams-team-main {
    display: flex;
    flex-flow: column;
    height: calc(100vh - 100px);
}

.teams-team-menu .team-subject {
    display: flex;
    flex-flow: column;
    background: #575757;
    width: 100%;
    border-radius: 3px;
    margin: 10px 0;
}

.teams-team-menu .team-subject .team-header {
    padding: 8px;
    text-align: start;
    background: #424242;
}

.teams-team-menu .team-subject .team-header h1 {
    font-size: 20px;
    text-transform: uppercase;
}

.teams-team-menu .team-subject .team-content {
    padding: 4px;
    display: flex;
    justify-content: space-around;
}

.teams-team-menu .team-subject .team-content button {
    flex: 1;
    padding: 5px;
    outline: none;
    border: none;
    text-transform: uppercase;
    font-weight: bold;
    font-size: 14px;
    background-color: #c32222;
    border-radius: 3px;
    color: #e7e7e7;
    margin: 5px;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.teams-team-menu .team-subject .team-content button.green {
    background-color: #4CAF50;
}

.teams-team-menu .team-invite {
    display: flex;
    align-items: center;
    width: 100%;
}

.teams-team-menu .team-invite button {
    flex: 2 !important;
}

.teams-team-menu .team-invite .Input-text {
    flex: 1;
    margin: 0;
    width: 100%;
    height: 30px;
    border: none;
    border-radius: 3px;
    margin: 5px;
    padding: 5px;
}

.teams-team-menu .team-invite input[type=number]::-webkit-inner-spin-button,
.teams-team-menu .team-invite input[type=number]::-webkit-outer-spin-button {
    -webkit-appearance: none;
    -moz-appearance: none;
    margin: 0;
}

.teams-team-menu .team-invite input[type=number] {
    -webkit-appearance: none;
    -moz-appearance: none;
    box-shadow: none !important;
}

.teams-team-menu .team-invite input[type=number]:active {
    border-bottom-color: #c32222 !important;
}
  
.teams-team-menu .Input-text:focus {
    outline: none;
    box-shadow: 0.2rem 0.8rem 1.6rem var(--colorPrimary600);
}

.teams-team-menu .players {
    width: 100%;
    border-top: 1px solid #2e2e2e;
}

.teams-team-menu .players .player {
    display: flex;
    align-items: center;
    border-bottom: 1px solid #2e2e2e;
    min-height: 40px;
}

.teams-team-menu .players .player p {
    flex: 2;
    font-size: 18px;
    word-wrap: break-word;
}

.teams-team-menu .players .player button {
    flex: 1;
    padding: 5px;
    outline: none;
    border: none;
    text-transform: uppercase;
    font-weight: bold;
    font-size: 14px;
    background-color: #c32222;
    border-radius: 3px;
    color: #e7e7e7;
    margin: 5px;
}

.teams-team-menu .no-players {
    width: 100%;
}

.teams-team-menu .no-players h1 {
    font-size: 22px;
    text-transform: uppercase;
    padding: 5px 0;
}

.teams-team-menu .team-invites {
    width: 100%;
    border-top: 1px solid #2e2e2e;
}

.teams-team-menu .team-invites .invite {
    display: flex;
    align-items: center;
    border-bottom: 1px solid #2e2e2e;
    min-height: 40px;
}

.teams-team-menu .team-invites .invite p {
    flex: 2;
    font-size: 18px;
    word-wrap: break-word;
}

.teams-team-menu .team-invites .invite button {
    flex: 1;
    padding: 5px;
    outline: none;
    border: none;
    text-transform: uppercase;
    font-weight: bold;
    font-size: 14px;
    background-color: #c32222;
    border-radius: 3px;
    color: #e7e7e7;
    margin: 5px;
}

.teams-team-menu .team-no-invites {
    width: 100%;
}

.teams-team-menu .team-no-invites h1 {
    font-size: 22px;
    text-transform: uppercase;
    padding: 5px 0;
}

/* RACING */

.racing-no-races {
    display: flex;
    flex-flow: column;
    margin: auto;
    align-items: center;
    justify-content: center;
    height: calc(100vh - 100px);
}

.racing-no-races img {
    width: 200px;
    margin-bottom: 10px;
}

.racing-no-races h1 {
    font-size: 1.5em;
    text-transform: uppercase;
}

.racing-ongoing {
    display: flex;
    flex-flow: column;
    margin: auto;
    align-items: center;
    justify-content: center;
    height: calc(100vh - 100px);
}

.racing-ongoing img {
    width: 200px;
    margin-bottom: 10px;
}

.racing-ongoing button {
    margin-top: 15px;
    background-color: #c32222;
    border-radius: 3px;
    color: #e7e7e7;
    padding: 10px 40px;
    outline: none;
    border: none;
    text-transform: uppercase;
    font-weight: bold;
    font-size: 18px;
}

.racing-ongoing h1 {
    font-size: 1.5em;
    text-transform: uppercase;
}

.racing-subject {
    display: flex;
    flex-flow: column;
    background: #575757;
    width: 100%;
    border-radius: 3px;
    margin: 10px 0;
}

.racing-subject button.collapsible {
    display: flex;
    background-color: #424242;
    color: white;
    cursor: pointer;
    padding: 8px;
    width: 100%;
    border: none;
    text-align: left;
    outline: none;
    font-weight: bold;
    font-size: 20px;
    text-transform: uppercase;
}

.racing-subject button.collapsible p.text {
    flex: 3;
}

.racing-subject button.collapsible p {
    flex: 1;
}

.pulse {
    animation: pulsate 3s ease-out;
    animation-iteration-count: infinite;
    -webkit-animation: pulsate 3s ease-out;
    -webkit-animation-iteration-count: infinite;
    opacity: 0.5;
}

@keyframes pulsate {
    0% { 
        opacity: 0.5;
    }
    50% { 
        opacity: 1.0;
    }
    100% { 
        opacity: 0.5;
    }
}

.racing-subject button.collapsible p.green {
    color: #228B22;
}

.racing-subject button.collapsible p.red {
    color: #FF0000;
}

.racing-subject button.collapsible:after {
    content: '\002B';
    color: white;
    font-weight: bold;
    float: right;
    margin-left: 5px;
}

.racing-subject button.collapsible.active:after {
    content: "\2212";
}

.racing-subject .racing-content {
    max-height: 0;
