<template>
    <div id="App" v-if="opened || isRacing || isLoading || hasCountdown">
        <Racing v-if="isRacing" :isRacing="isRacing" :NameResource="NameResource" :prependZero="prependZero" :close="close"/>

        <div class="loader-container" v-if="isLoading">
            <div class="background"></div>
            <div class="sk-fading-circle">
                <div class="sk-circle1 sk-circle"></div>
                <div class="sk-circle2 sk-circle"></div>
                <div class="sk-circle3 sk-circle"></div>
                <div class="sk-circle4 sk-circle"></div>
                <div class="sk-circle5 sk-circle"></div>
                <div class="sk-circle6 sk-circle"></div>
                <div class="sk-circle7 sk-circle"></div>
                <div class="sk-circle8 sk-circle"></div>
                <div class="sk-circle9 sk-circle"></div>
                <div class="sk-circle10 sk-circle"></div>
                <div class="sk-circle11 sk-circle"></div>
                <div class="sk-circle12 sk-circle"></div>
            </div>
            <h1>{{loadingMessage}}</h1>
        </div>

        <div class="countdown" v-if="hasCountdown">
            <div class="countdown-wrapper" :class="countdown == 'GO' ? 'last' : ''">
                <h1>{{countdown}}</h1>
            </div>
        </div>

        <div class="teams" v-if="opened">
            <div class="teams-menu">
                <div class="notification">
                    <notifications animation-type="velocity" group="alert"/>
                </div>
                <div class="teams-header">
					<div class="close-menu" @click="close()"><span>x</span></div>
					<div class="logo-menu"><h1>Danish<span>RP</span></h1></div>
				</div>

                <Team v-if="!IsObjectEmpty(Team)" :StartLoading="StartLoading" :Team="Team" :Races="Races" :source="source" :playerJob="playerJob" :isRacing="isRacing" :MaxMembers="MaxMembers" :NameResource="NameResource" :prependZero="prependZero" :close="close"/>
                <NoTeam v-if="IsObjectEmpty(Team)" :StartLoading="StartLoading" :MaxMembers="MaxMembers" :OpenTeams="OpenTeams" :TeamInvites="TeamInvites" :NameResource="NameResource" :close="close"/>
            </div>
        </div>
    </div>
</template>

<script>

import axios from 'axios';
import Team from './screens/Team';
import NoTeam from './screens/NoTeam';
import Racing from './screens/Racing';

export default {
    components: {
        Team,
        NoTeam,
        Racing,
    },

    data: function(){
        return({
            opened: false,
            isRacing: false,
            isLoading: false,
            loadingMessage: '',
            hasCountdown: false,
            countdown: 0,
            NameResource: "drp_teams",
            source: 1,
            playerJob: 'none',
            MaxMembers: 30,
            Races: [],
            Team: [],
            OpenTeams: [],
            TeamInvites: [],
        })
    },

    methods: {
        receiveLua: function(event){
            if(!event || !event.data) return;
            const lua = event.data;

            if(lua.init) {
                this.source = lua.source;
                this.playerJob = lua.playerJob;
                this.Races = lua.Races;
                this.MaxMembers = lua.MaxMembers;
                this.NameResource = lua.NameResource;
            }

            if(lua.open) {
                this.opened = true;
            }
            
            if(lua.close) {
                this.close();
            }

            if(lua.refreshJob) {
                this.playerJob = lua.playerJob;
            }

            if(lua.refreshTeam) {
                this.Team = lua.Team;
            }

            if(lua.refreshInvites) {
                this.TeamInvites = lua.TeamInvites;
            }

            if(lua.refreshHubs) {
                this.OpenTeams = lua.OpenTeams;
            }

            if(lua.refreshRaces) {
                this.Races = lua.Races;
            }

            if(lua.showRaceStats) {
                this.isRacing = true;
                this.close();
            }

            if(lua.hideRaceStats) {
                this.isRacing = false;
            }

            if(lua.ToggleRaceActive) {
                this.ToggleRaceActive(lua.raceID)
            }

            if(lua.StartLoading) {
                this.StartLoading(lua.message, lua.timeout, lua.closeAfter);
            }

            if(lua.StopLoading) {
                this.StopLoading();
            }

            if(lua.ShowNotification) {
                this.ShowNotification(lua.type, lua.title, lua.text, lua.duration);
            }

            if(lua.startCountdown) {
                this.startCountdownTimer(lua.time);
            }
        },

        isArrayEmpty: function(array) {
            return array.length <= 0;
        },

        IsObjectEmpty: function(obj) {
            return Object.keys(obj).length <= 0;
        },

        GetRaceByID: function(raceID) {
            for (let key in this.Races) {
                let race = this.Races[key];
                if(race.id == raceID) {
                    return race;
                }
            }

            return null;
        },

        GetRaceKeyByID: function(raceID) {
            for (let key in this.Races) {
                let race = this.Races[key];
                if(race.id == raceID) {
                    return key;
                }
            }

            return null;
        },

        ToggleRaceActive: function(raceID) {
            let race = this.GetRaceByID(raceID)
            race.isActive = !race.isActive;
        },

        StartLoading: function(message, timeout, closeAfter) {
            if(closeAfter == null) closeAfter = false;
            if(timeout == null) timeout = 5000;
            var v = this;

            v.isLoading = true;
            v.loadingMessage = message;

            const NameResource = v.NameResource[0];
            axios.post(`https://${NameResource}/isLoading`, { 
                isLoading: v.isLoading 
            }).catch(function (error) {
                console.log(error + ' : Error at isLoading');
            });

            setTimeout(function() {
                if(!v.isLoading) return;

                v.isLoading = false;
                v.loadingMessage = '';

                const NameResource = v.NameResource[0];
                axios.post(`https://${NameResource}/isLoading`, { 
                    isLoading: v.isLoading
                }).catch(function (error) {
                    console.log(error + ' : Error at isLoading');
                });

                if(closeAfter) v.close();
            }, timeout);
        },

        StopLoading: function() {
            this.isLoading = false;
            this.loadingMessage = '';

            const NameResource = this.NameResource[0];
            axios.post(`https://${NameResource}/isLoading`, { 
                isLoading: this.isLoading
            }).catch(function (error) {
                console.log(error + ' : Error at isLoading');
            });
        },

        prependZero: function(time, length) {
            time = new String(time);
            time = time.substring(0, length);
            return new Array(Math.max(length - time.length + 1, 0)).join("0") + time;
        },

        ShowNotification: function(type, title, text, duration) {
            if(duration == null) duration = 5000;

            this.$notify({
                group: 'alert',
                type: type,
                title: title,
                text: text,
                duration: duration,
            });
        },

        startCountdownTimer: function(time) {
            this.hasCountdown = true;
            this.countdownTimer(time);
        },

        countdownTimer: function(time) {
            let v = this;
            if(time) v.countdown = time;
            
            if(v.countdown > 0) {
                setTimeout(() => {
                    v.countdown -= 1
                    v.countdownTimer()
                }, 1000)
            }

            if(v.countdown <= 0) {
                v.countdown = "GO";

                const NameResource = v.NameResource[0];
                axios.post(`https://${NameResource}/finishCountdown`).catch(function (error) {
                    console.log(error + ' : Error at finishCountdown');
                });

                setTimeout(() => {
                    v.countdown = 0;
                    v.hasCountdown = false;
                }, 2000)
            }
        },

        close: function() {     
            if(this.closed) return;

            const NameResource = this.NameResource[0];
            axios.post(`https://${NameResource}/close`).catch(function (error) {
                console.log(error + ' : Error at close'); 
            });

            this.opened = false;
        },

        keyPress: function(event) {
            const key = event.key;
            
            if(key == "Escape"){
                this.close();
            }
        },
    },

    created: function(){
        window.addEventListener('message', this.receiveLua);  
        window.addEventListener('keydown', this.keyPress);
    },
    
    destroyed: function(){
        window.removeEventListener('message', this.receiveLua);
        window.removeEventListener('keydown', this.keyPress);
    },
}
</script>

<style>
* {
	padding: 0; 
	margin: 0; 
	box-sizing: border-box; 
    font-family: Roboto;
    overflow: hidden;
}

button:hover {
    cursor: pointer;
}

.loader-container {
    position: absolute;
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-flow: column;
    justify-content: center;
    align-items: center;
    z-index: 5000;
}

.loader-container .background {
    background-color:rgba(0, 0, 0, 0.6);
    position: absolute;
    width: 100vw;
    height: 100vh;
}

.loader-container .loader {
    border: 16px solid #f3f3f3;
    border-top: 16px solid #ae1e1e;
    border-radius: 50%;
    width: 120px;
    height: 120px;
    animation: spin 2s linear infinite;
    z-index: 99;
}

.loader-container h1 {
    margin-top: 20px;
    text-transform: uppercase;
    font-weight: bold;
    font-size: 26px;
    color: #e7e7e7;
    z-index: 99;
}

.sk-fading-circle {
	margin: 10px auto;
	width: 120px;
	height: 120px;
	position: relative;
}

.sk-fading-circle .sk-circle {
	width: 100%;
	height: 100%;
	position: absolute;
	left: 0;
	top: 0;
}

.sk-fading-circle .sk-circle:before {
	content: '';
	display: block;
	margin: 0 auto;
	width: 15%;
	height: 15%;
	background-color: #e7e7e7;
	border-radius: 100%;
	-webkit-animation: sk-circleFadeDelay 1.2s infinite ease-in-out both;
	animation: sk-circleFadeDelay 1.2s infinite ease-in-out both;
}

.sk-fading-circle .sk-circle2 {
	-webkit-transform: rotate(30deg);
	-ms-transform: rotate(30deg);
	transform: rotate(30deg);
}

.sk-fading-circle .sk-circle3 {
	-webkit-transform: rotate(60deg);
	-ms-transform: rotate(60deg);
	transform: rotate(60deg);
}

.sk-fading-circle .sk-circle4 {
	-webkit-transform: rotate(90deg);
	-ms-transform: rotate(90deg);
	transform: rotate(90deg);
}

.sk-fading-circle .sk-circle5 {
	-webkit-transform: rotate(120deg);
	-ms-transform: rotate(120deg);
	transform: rotate(120deg);
}

.sk-fading-circle .sk-circle6 {
	-webkit-transform: rotate(150deg);
	-ms-transform: rotate(150deg);
	transform: rotate(150deg);
}

.sk-fading-circle .sk-circle7 {
	-webkit-transform: rotate(180deg);
	-ms-transform: rotate(180deg);
	transform: rotate(180deg);
}

.sk-fading-circle .sk-circle8 {
	-webkit-transform: rotate(210deg);
	-ms-transform: rotate(210deg);
	transform: rotate(210deg);
}

.sk-fading-circle .sk-circle9 {
	-webkit-transform: rotate(240deg);
	-ms-transform: rotate(240deg);
	transform: rotate(240deg);
}

.sk-fading-circle .sk-circle10 {
	-webkit-transform: rotate(270deg);
	-ms-transform: rotate(270deg);
	transform: rotate(270deg);
}

.sk-fading-circle .sk-circle11 {
	-webkit-transform: rotate(300deg);
	-ms-transform: rotate(300deg);
	transform: rotate(300deg);
}

.sk-fading-circle .sk-circle12 {
	-webkit-transform: rotate(330deg);
	-ms-transform: rotate(330deg);
	transform: rotate(330deg);
}

.sk-fading-circle .sk-circle2:before {
	-webkit-animation-delay: -1.1s;
	animation-delay: -1.1s;
}

.sk-fading-circle .sk-circle3:before {
	-webkit-animation-delay: -1s;
	animation-delay: -1s;
}

.sk-fading-circle .sk-circle4:before {
	-webkit-animation-delay: -0.9s;
	animation-delay: -0.9s;
}

.sk-fading-circle .sk-circle5:before {
	-webkit-animation-delay: -0.8s;
	animation-delay: -0.8s;
}

.sk-fading-circle .sk-circle6:before {
	-webkit-animation-delay: -0.7s;
	animation-delay: -0.7s;
}

.sk-fading-circle .sk-circle7:before {
	-webkit-animation-delay: -0.6s;
	animation-delay: -0.6s;
}

.sk-fading-circle .sk-circle8:before {
	-webkit-animation-delay: -0.5s;
	animation-delay: -0.5s;
}

.sk-fading-circle .sk-circle9:before {
	-webkit-animation-delay: -0.4s;
	animation-delay: -0.4s;
}

.sk-fading-circle .sk-circle10:before {
	-webkit-animation-delay: -0.3s;
	animation-delay: -0.3s;
}

.sk-fading-circle .sk-circle11:before {
	-webkit-animation-delay: -0.2s;
	animation-delay: -0.2s;
}

.sk-fading-circle .sk-circle12:before {
	-webkit-animation-delay: -0.1s;
	animation-delay: -0.1s;
}

@-webkit-keyframes sk-circleFadeDelay {
	0%,
	39%,
	100% {
		opacity: 0;
	}
	40% {
		opacity: 1;
	}
}

@keyframes sk-circleFadeDelay {
	0%,
	39%,
	100% {
		opacity: 0;
	}
	40% {
		opacity: 1;
	}
}

.countdown {
    position: absolute;
    display: flex;
    width: 100vw;
    height: 70vh;
    justify-content: center;
    align-items: center;
    z-index: 0;
}

.countdown-wrapper {
    display: flex;
    width: 140px;
    height: 140px;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    background-color: rgba(255, 0, 0, .5);
}

.countdown-wrapper.last {
    background-color: rgba(34, 139, 34, .5);
}

.countdown-wrapper h1 {
    color: #f3f3f3;
    font-weight: bold;
    font-size: 4.5em;
}

.teams {
    display: flex;
    width: 100vw;
    height: 100vh;
    justify-content: flex-end;
    z-index: 10;
    animation: fadeIn .5s;
    -webkit-animation: fadeIn .5s;
    -moz-animation: fadeIn .5s;
    -o-animation: fadeIn .5s;
    -ms-animation: fadeIn .5s;
}

@keyframes fadeIn {
  0% {opacity:0;}
  100% {opacity:1;}
}

@-moz-keyframes fadeIn {
  0% {opacity:0;}
  100% {opacity:1;}
}

@-webkit-keyframes fadeIn {
  0% {opacity:0;}
  100% {opacity:1;}
}

@-o-keyframes fadeIn {
  0% {opacity:0;}
  100% {opacity:1;}
}

@-ms-keyframes fadeIn {
  0% {opacity:0;}
  100% {opacity:1;}
}

.teams-container {
    min-height: 100vh;
    align-items: center;
    justify-content: flex-end;
}

.teams-menu {
    position: relative;
    display: flex;
    flex-flow: column;
    width: 500px;
    height: 100vh;
    color: #e7e7e7;
    background-color: #2e2e2e;
    padding: 5px;
    text-align: center;
}

.notification .vue-notification-group {
    display: block;
    position: absolute;
    right: 100px !important;
    z-index: 5000;
}

.teams-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 5px 5px 5px;
    border-bottom: 2px solid #575757;
}

.teams-header .close-menu {
    font-size: 23px;
    font-weight: bold;
}

.teams-header .close-menu:hover {
    cursor: pointer;
}

.teams-header .logo-menu h1 {
    font-size: 21px;
}

.teams-header .logo-menu span {
    color: #FF0000;
}
</style>