<template>
    <div class="race-scoreboard">
		<div class="race-container">
			<div class="race-column" id="lap">
				<h1>Pos</h1><div class="stats"><span>{{currentPos}}</span>/<span>{{currentMaxPos}}</span></div>
			</div>
			<div class="race-column" id="lap">
				<h1>Laps</h1><div class="stats"><span>{{currentLap}}</span>/<span>{{currentMaxLap}}</span></div>
			</div>
			<div class="race-column" id="checkpoint">
				<h1>Checkpoint</h1><div class="stats"><span>{{currentCheck}}</span>/<span>{{currentMaxCheck}}</span></div>
			</div>
			<div class="race-column" id="curent">
				<h1>Current Lap</h1><div class="stats"><span>{{currentMin}}</span>:<span>{{currentSec}}</span>:<span>{{currentMS}}</span></div>
			</div>
			<div class="race-column" id="best">
				<h1>Best Lap</h1><div class="stats"><span>{{bestMin}}</span>:<span>{{bestSec}}</span>:<span>{{bestMS}}</span></div>
			</div>
			<div class="race-column" id="total">
				<h1>Total Time</h1><div class="stats"><span>{{totalMin}}</span>:<span>{{totalSec}}</span>:<span>{{totalMS}}</span></div>
			</div>
		</div>
	</div>
</template>

<script>
import axios from 'axios';

export default {
    props: {
        isRacing: {},
        prependZero: {},
        NameResource: {},
        close: {},
    },

    data: function(){
        return({
            raceid: 0,
            currentPos: 0,
            currentMaxPos: 0,
            currentLap: 0,
            currentMaxLap: 0,
            currentCheck: 0,
            currentMaxCheck: 0,
            currentTimeUpdate: 0,
            startCurrentTime: 0,
            totalTimeUpdate: 0,
            startTotalTime: 0,
            bestTimeElapsed: 0,
            currentMin: this.prependZero(0, 2),
            currentSec: this.prependZero(0, 2),
            currentMS: this.prependZero(0, 3),
            bestMin: this.prependZero(0, 2),
            bestSec: this.prependZero(0, 2),
            bestMS: this.prependZero(0, 3),
            totalMin: this.prependZero(0, 2),
            totalSec: this.prependZero(0, 2),
            totalMS: this.prependZero(0, 3),
        })
    },

    methods: {
        receiveLua: function(event){
            if(!event || !event.data) return;
            const lua = event.data;

            if(lua.resetStats) {
                this.setStopwatch("current", 0, 0, 0);
                this.setStopwatch("best", 0, 0, 0);
                this.setStopwatch("total", 0, 0, 0);
            }

            if(lua.startRacing) {
                if(!this.isRacing) return;

                if(lua.raceid) this.raceid = lua.raceid;
                this.startRacing();
            }

            if(lua.stopRacing) {
                if(!this.isRacing) return;

                if(lua.forceStop == false) {
                    this.newRacingLap();
                    this.SyncRaceStats(lua);
                }

                this.stopRacing();
            }

            if(lua.newLap) {
                if(!this.isRacing) return;

                this.newRacingLap();
                this.SyncRaceStats(lua);
            }

            if(lua.SyncRaceStats) {
                this.SyncRaceStats(lua);
            }
        },

        startRacing: function() {
            this.updateCurrentTime();
            this.updateTotalTime();
        },

        stopRacing: function() {
            var v = this;
            if(v.currentTimeUpdate) clearInterval(v.currentTimeUpdate);
            if(v.totalTimeUpdate) clearInterval(v.totalTimeUpdate);

            v.currentTimeUpdate = 0;
            v.startCurrentTime = 0;
            v.totalTimeUpdate = 0;
            v.startTotalTime = 0;
            v.bestTimeElapsed = 0;
        },

        SyncRaceStats: function(data) {
            if(data.position) this.currentPos = data.position;
            if(data.maxPosition) this.currentMaxPos = data.maxPosition;
            if(data.lap) this.currentLap = data.lap;
            if(data.maxLap) this.currentMaxLap = data.maxLap;
            if(data.checkpoint) this.currentCheck = data.checkpoint;
            if(data.maxCheckpoints) this.currentMaxCheck = data.maxCheckpoints;
        },

        newRacingLap: function() {
            var v = this;

            if(v.currentTimeUpdate) clearInterval(v.currentTimeUpdate);
            v.setStopwatch("current", 0, 0, 0);
            
            if(v.startCurrentTime != 0) {
                const NameResource = v.NameResource[0];
                var timeElapsed = new Date().getTime() - v.startCurrentTime.getTime();

                if(v.bestTimeElapsed != 0) {
                    let timeDifference = timeElapsed - v.bestTimeElapsed;
                    
                    let minutesDeff = parseInt(timeDifference / 1000 / 60);
                    if (minutesDeff >= 60) minutesDeff %= 60;
                    
                    let secondsDeff = parseInt(timeDifference / 1000);
                    if (secondsDeff >= 60) secondsDeff %= 60;
                    
                    let millisecondsDef = timeDifference;
                    if (millisecondsDef >= 1000) millisecondsDef %= 1000;
                    
                    let type = 'slower';
                    if(millisecondsDef < 0) {
                        type = 'faster';
                    }

                    let minOutput = Math.abs(minutesDeff);
                    let secOutput = Math.abs(secondsDeff);
                    let msOutput = Math.abs(millisecondsDef);

                    axios.post(`https://${NameResource}/timeStatus`, {
                        type: type, 
                        minutes: v.prependZero(minOutput, 2), 
                        seconds: v.prependZero(secOutput, 2), 
                        milliseconds: v.prependZero(msOutput, 3)
                    }).catch(function (error) {
                        console.log(error + ' : Error at timeStatus');
                    });
                }

                if(v.bestTimeElapsed == 0 || v.bestTimeElapsed > timeElapsed) {
                    let minutes = parseInt(timeElapsed / 1000 / 60);
                    if (minutes >= 60) minutes %= 60;
                    
                    let seconds = parseInt(timeElapsed / 1000);
                    if (seconds >= 60) seconds %= 60;
                    
                    let milliseconds = timeElapsed;
                    if (milliseconds >= 1000) milliseconds %= 1000;
                    
                    v.bestTimeElapsed = timeElapsed;
                    v.setStopwatch("best", minutes, seconds, milliseconds);
                }

                axios.post(`https://${NameResource}/newTimeLap`, {
                    raceid: v.raceid, time: timeElapsed
                }).catch(function (error) {
                    console.log(error + ' : Error at newTimeLap');
                });

                v.currentTimeUpdate = 0;
                v.updateCurrentTime();
            }
        },

        updateCurrentTime: function() {
            var v = this;
            if(v.currentTimeUpdate) return;

            v.startCurrentTime = new Date();
            v.currentTimeUpdate = setInterval(function() {
                var timeElapsed = new Date().getTime() - v.startCurrentTime.getTime();
                
                let minutes = parseInt(timeElapsed / 1000 / 60);
                if (minutes >= 60) minutes %= 60;
                
                let seconds = parseInt(timeElapsed / 1000);
                if (seconds >= 60) seconds %= 60;
                
                let milliseconds = timeElapsed;
                if (milliseconds >= 1000) milliseconds %= 1000;
                
                v.setStopwatch("current", minutes, seconds, milliseconds);
            }, 25);
        },

        updateTotalTime: function() {
            var v = this;
            if(v.totalTimeUpdate) return;

            v.startTotalTime = new Date();
            v.totalTimeUpdate = setInterval(function() {
                var timeElapsed = new Date().getTime() - v.startTotalTime.getTime();

                let minutes = parseInt(timeElapsed / 1000 / 60);
                if (minutes >= 60) minutes %= 60;
                
                let seconds = parseInt(timeElapsed / 1000);
                if (seconds >= 60) seconds %= 60;
                
                let milliseconds = timeElapsed;
                if (milliseconds >= 1000) milliseconds %= 1000;
                
                v.setStopwatch("total", minutes, seconds, milliseconds);
            }, 25);
        },

        setStopwatch: function(type, minutes, seconds, milliseconds) {
            if (type == "current") {
                this.currentMin = this.prependZero(minutes, 2);
                this.currentSec = this.prependZero(seconds, 2);
                this.currentMS = this.prependZero(milliseconds, 3);
            } 
            
            if (type == "best") {
                this.bestMin = this.prependZero(minutes, 2);
                this.bestSec = this.prependZero(seconds, 2);
                this.bestMS = this.prependZero(milliseconds, 3);
            }
            
            if (type == "total") {
                this.totalMin = this.prependZero(minutes, 2);
                this.totalSec = this.prependZero(seconds, 2);
                this.totalMS = this.prependZero(milliseconds, 3);
            }
        },

        keyPress: function(event) {
            const key = event.key;
            
            if(key == "Escape"){
                this.close();
            }
        },
    },

    created: function(){
        window.addEventListener('message', this.receiveLua);  
        window.addEventListener('keydown', this.keyPress);
    },
    
    destroyed: function(){
        window.removeEventListener('message', this.receiveLua);
        window.removeEventListener('keydown', this.keyPress);
    },
}
</script>

<style>
.race-scoreboard {
    display: flex;
    flex-flow: column;
    justify-content: center;
    align-items: center;
    width: 300px;
    height: 100vh;
    margin-left: 20px;
    position: absolute;
}

.race-scoreboard .race-container {
    width: 100%;
    padding: 5px;
    background-color:rgba(0, 0, 0, 0.3);
}

.race-scoreboard .race-container .race-column {
    width: 100%;
    display: flex;
    padding: 5px;
    color: #eee;
    font-size: 23px;
    font-weight: bold;
    border-radius: 2px;
}

.race-scoreboard .race-container .race-column h1 {
    flex: 1;
    font-size: 23px;
}

.race-scoreboard .race-container .race-column .stats {
    flex: 1;
    display: flex;
    justify-content: flex-end;
    align-items: center;
}
</style>